// Utility functions for the student dashboard

/**
 * Get the current time-based greeting (morning, afternoon, evening)
 * @returns {string} - "morning", "afternoon", or "evening"
 */
export const getTimeOfDay = () => {
  const hour = new Date().getHours();
  
  if (hour >= 5 && hour < 12) {
    return "morning";
  } else if (hour >= 12 && hour < 18) {
    return "afternoon";
  } else {
    return "evening";
  }
};

/**
 * Get the daily welcome message based on the day of the week
 * @param {Array} messages - Array of 7 messages (one for each day)
 * @returns {string} - The message for the current day
 */
export const getDailyMessage = (messages) => {
  const dayOfWeek = new Date().getDay(); // 0 (Sunday) to 6 (Saturday)
  return messages[dayOfWeek];
};

/**
 * Get complete greeting data for the welcome banner
 * @param {Object} greetingData - Object containing greeting configurations
 * @param {string} username - User's name
 * @returns {Object} - { greetingText, mascotImage }
 */
export const getGreetingData = (greetingData, username) => {
  const timeOfDay = getTimeOfDay();
  const data = greetingData[timeOfDay];
  
  return {
    greetingText: `${data.greeting}, ${username}!`,
    mascotImage: data.mascot,
    fallbackMascot: data.fallbackMascot
  };
};

/**
 * Format time for display
 * @param {Date} date - Date object
 * @returns {string} - Formatted time string
 */
export const formatTime = (date) => {
  return date.toLocaleTimeString("en-US", {
    hour: "numeric",
    minute: "2-digit",
    hour12: true,
  });
};

/**
 * Format date for display
 * @param {Date} date - Date object
 * @returns {string} - Formatted date string
 */
export const formatDate = (date) => {
  return date.toLocaleDateString("en-US", {
    weekday: "long",
    month: "long",
    day: "numeric",
    year: "numeric",
  });
};
